import React, { useState } from 'react';
import "./header.css";

const Header = () => {
        /* Menu desplegable */    
        const[Toggle, showMenu] = useState(false);
        
    return (
        <header className= "header">
            <nav className= "nav container">
                <a href="index.html" className="nav__logo">Julio - Sitio en construcción</a>

                <div className={Toggle ? "nav__menu show-menu" : "nav__menu"}>
                    <ul className="nav__list grid">
                        <li className="nav__item">
                            <a href="#home" className="nav__link active-link">
                                <i className="uil uil-estate nav__icon"></i> Inicio
                            </a>
                        </li>
                        <li className="nav__item">
                            <a href="#about" className="nav__link">
                                <i className="uil uil-user nav__icon"></i> Sobre mi
                            </a>
                        </li>
                        <li className="nav__item">
                            <a href="#skills" className="nav__link">
                                <i className="uil uil-check-circle nav__icon"></i> Habilidades
                            </a>
                        </li>
                        <li className="nav__item">
                            <a href="#services" className="nav__link">
                                <i className="uil uil-briefcase-alt nav__icon"></i> Experiencias
                            </a>
                        </li>
                        <li className="nav__item">
                            <a href="#portfolio" className="nav__link">
                                <i className="uil uil-slider-h nav__icon"></i> Trayectoria
                            </a>
                        </li>
                        <li className="nav__item">
                            <a href="#testimonials" className="nav__link">
                                <i className="uil uil-file-bookmark-alt nav__icon"></i> Certificaciones
                            </a>
                        </li>
                        <li className="nav__item">
                            <a href="#contact" className="nav__link">
                                <i className="uil uil-message nav__icon"></i> Contacto
                            </a>
                        </li>
                    </ul>

                    <i className="uil uil-times nav__close" onClick={() => showMenu (!Toggle)}></i>
                </div>

                <div className="nav__toggle" onClick={() => showMenu (!Toggle)}>
                    <i class="uil uil-apps"></i>
                </div>
            </nav>
        </header>
    )
}

export default Header