import React, { useState } from 'react'
import "./trayectoria.css"

const Trayectoria = () => {
    const [toggleState, setToggleState] = useState(1);

    const toggleTab = (index) => {
        setToggleState(index);
    };

  return (
    <section className="qualification section" id="portfolio">
        <h2 className="section__title">Trayectoria</h2>
            <span className="section__subtitle">Académica y profesional</span>

            <div className="qualification__container container">
                <div className="qualification__tabs">
                    
                    <div className={toggleState === 1 ? "qualification__button qualification__activate button--flex" : "qualification__button button--flex"} onClick={() => toggleTab(1)}>
                        <i className="uil uil-graduation-cap qualification__icon"></i> Educación
                    </div>

                    <div className={toggleState === 2 ? "qualification__button qualification__activate button--flex" : "qualification__button button--flex"} onClick={() => toggleTab(2)}>
                        <i className="uil uil-briefcase-alt qualification__icon"></i> Experiencia
                    </div>
                </div>

                    <div className="qualification__sections"> 
                        
                        {/* Educación */}
                        <div className={toggleState === 1 ? "qualification__content qualification__content-active" : "qualification__content"}>
                            <div className="qualification__data">
                                <div>
                                <h3 className="qualification__title">Front-End</h3>
                                    <span className="qualification__subtitle">Oracle - ONE</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i> 2024 - Actualidad
                                    </div>
                                </div>

                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                            </div>
                            <div className="qualification__data">
                                <div></div>
                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                                <div>
                                <h3 className="qualification__title">Back-End</h3>
                                    <span className="qualification__subtitle">Oracle - ONE</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i> 2023 - 2024
                                    </div>
                                </div>
                            </div>
                            <div className="qualification__data">
                                <div>
                                <h3 className="qualification__title">Ingeniería en Informática</h3>
                                    <span className="qualification__subtitle">IPN - UPIICSA</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i> 2019 - 2024
                                    </div>
                                </div>

                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                            </div>
                            <div className="qualification__data">
                                <div></div>
                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                                <div>
                                <h3 className="qualification__title">Técnico en Sistemas Digitales</h3>
                                    <span className="qualification__subtitle">IPN - CECyT 9</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i> 2015 - 2018
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                        {/* Experiencias */}
                        <div className={toggleState === 2 ? "qualification__content qualification__content-active" : "qualification__content"}>
                            <div className="qualification__data">
                                <div>
                                <h3 className="qualification__title">Analista Programador</h3>
                                    <span className="qualification__subtitle">Sanborns - Becario</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i>  Abril 2024 - Actualidad
                                    </div>
                                </div>

                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                            </div>
                            <div className="qualification__data">
                                <div></div>
                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                                <div>
                                <h3 className="qualification__title">Implementador</h3>
                                    <span className="qualification__subtitle">PowerStreet - PP</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i> Feb/Abr 2024
                                    </div>
                                </div>
                            </div>
                            <div className="qualification__data">
                                <div>
                                <h3 className="qualification__title">Solution Architect</h3>
                                    <span className="qualification__subtitle">BBVA - Becario</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i> 2022 - 2023
                                    </div>
                                </div>

                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                            </div>
                            <div className="qualification__data">
                                <div></div>
                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                                <div>
                                <h3 className="qualification__title">Soporte Técnico</h3>
                                    <span className="qualification__subtitle">SIBOLTRA - IPN</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i> 2021 - 2022
                                    </div>
                                </div>
                            </div>
                            <div className="qualification__data">
                                <div>
                                <h3 className="qualification__title">Técnico en Electrónica</h3>
                                    <span className="qualification__subtitle">MEMOVAM</span>
                                    <div className="qualification__calender">
                                        <i className="uil uil-calendar-alt"></i> 2018 - 2020
                                    </div>
                                </div>

                                <div>
                                    <span className="qualification__rounder"></span>
                                    <span className="qualification__line"></span>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
    </section>
  )
}

export default Trayectoria